@media (max-width: 1279px) {
  .section {
    width: 100%;
  }

  .education {
    flex-direction: column;
    align-items: center;

    > .cards-side {
      width: 700px;
    }

    > .picture {
      display: none;
    }
  }

  .pocket > .card-holder {
    width: 100%;
  }
}

@media (max-width: 1125px) {
  .section {
    flex-direction: column;
    align-items: center;

    > .cards-side {
      width: 700px;
    }

    .white-stopper {
      opacity: 0;
    }
  }

  .about > .picture {
    display: none;
  }

  .links {
    flex-direction: column-reverse;

    > .cards-side {
      margin-top: 30px;
    }

    > .picture {
      width: 700px;
    }
  }

  .games {
    > .picture {
      display: none;
    }
  }
}

@media (max-width: 899px) {

  .work {
    display: flex;
    flex-direction: column-reverse;
  }

  .pocket {
    height: fit-content;
    background-color: transparent;

    > .border-element-container {
      display: none;
    }

    > .card-holder {
      justify-content: center;
      transform: translate(0);

      > .pocket-side {
        display: flex;
        flex-direction: column;
        align-items: center;

        &:nth-child(2) {
          display: none;
        }

        &:nth-child(1) {
          width: 90%;
        }

        > .work-card-container {
          height: 100%;
          margin-bottom: 30px;

          &:hover {
            padding: 0;
          }

          &.sel {
            transform: translate(0);
          }
        }
      }
    }
  }
}

@media (max-width: 759px) {
  .section {
    > .cards-side {
      width: calc(100% - 61px);
    }

    > .picture {
      width: calc(100% - 61px);
    }

  }
  .img-style {
    flex-wrap: wrap;

    a {
      margin-top: 10px;
      height: 50px;
      width: 60%;
    }
  }
  .pocket {
    height: 100%;
  }
}

@media (max-width: 649px) {
  .about > .cards-side > .glass-card {
    height: 360px;
  }
  .links > .cards-side > .glass-card {
    height: 200px;

    > .card-title {
      width: 100%;
      transform: translate(0);

    }

    > .card-front {
      justify-content: center;
      align-items: flex-start;

      a {
        height: 50px;
        width: 60%;
      }
    }
  }
}

@media (max-width: 409px) {
  .games {
    .card-front {
      > :nth-child(2) {
        margin-top: 10px;
      }

      > img {
        width: 100%;
        height: auto;
      }

      a {
        height: 50px;
        width: 100%;
      }
    }
  }
}
