@import "variables";

.glass-card {
  position: relative;
  height: 100%;

  > .card-title {
    height: 100%;
    position: absolute;
    transform: translateX(20px);
    display: flex;
    align-items: flex-end;
    box-sizing: border-box;
    padding-bottom: 20px;
    justify-content: center;
    font-size: clamp(1em, 14vw, 70px);
    color: white;
  }

  > .card-front {
    height: 100%;
    min-height: 120px;
    background: rgba(255, 255, 255, 0.25);
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
    backdrop-filter: blur(5.0px);
    border-radius: 15px;
    border: 1px solid rgba(255, 255, 255, 0.18);
    padding: 20px;
    box-sizing: border-box;
    overflow: hidden;

    &.txt-style {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    &.column-style {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
    }

    &.button-style {
      display: flex;
      justify-content: flex-end;
    }

    &.img-style {
      display: flex;
      justify-content: space-evenly;
    }

    > img {
      border-radius: 5px;
      height: 100px;

      &:only-child {
        width: 100%;
        height: auto;
      }
    }
  }
}

.work-card-container {
  height: 0;
  margin-bottom: 80px;
  transition: 0.2s;
  cursor: pointer;

  > .description {
    display: none;
    width: 97%;
    text-align: center;
    padding: 15px;
    box-sizing: border-box;
    font-size: 0.9em;
    color: white;
  }

  &:hover {
    padding-left: 20px;
  }

  &.sel {
    padding-left: 0;

    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        transform: translateX(106%) translateY(100+($i*-80px));
      }
    }

    > .description {
      display: block;
    }
  }
}
