@import "variables";

@function transValue($height, $sign) {
  $step1: calc(#{$height} / 2);
  $step2: calc(-30px * #{$sign});
  $step3: calc(#{$step1} + #{$step2});
  @return calc(#{$step3} * #{$sign})
}

.pocket {
  height: $work-height;
  background-color: #00000040;
  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  > .border-element-container {
    position: absolute;
    height: 0;
    width: 100%;

    > .border-element {
      height: 30px;
    }

    &.top-border {
      transform: translateY(transValue($work-height, -1));

      > .border-element {
        background: linear-gradient(0deg, #00000040, #00000000);
      }
    }

    &.bottom-border {
      transform: translateY(transValue($work-height, 1));

      > .border-element {
        background: linear-gradient(0deg, #00000050, #00000000);
      }
    }
  }
}

.pocket > .card-holder {
  display: flex;
  justify-content: space-between;
  width: 1263px;
  padding-left: 1%;
  padding-right: 1%;
  box-sizing: border-box;
  transform: translateY(-50px);

  > .pocket-side {
    width: clamp(200px, 47vw, 600px);
    height: 100%;

    > .work-card-container {
      width: inherit;

      > .glass-card {
        height: fit-content;

        > .card-front {
          padding: 0;
        }
      }
    }
  }
}
