@import "variables";

.section {
  width: 1263px;
  display: flex;
  justify-content: space-evenly;
  margin: 0 auto;
  //padding: 0 calc((100% - 1024px) / 2);

  > div {
    margin-left: 30px;
    margin-right: 30px;
  }

  > .cards-side {
    flex: 4;
    display: flex;
    justify-content: space-between;
    flex-direction: column;

    > :nth-child(n) {
      margin-top: 30px;
    }

    > :first-child {
      margin-top: 0;
    }
  }

  > .picture {
    border-radius: 10px;
    //height: 100%;
    background-color: green;
    overflow: hidden;
    box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);

    display: flex;
    align-items: center;
    justify-content: center;

    &:hover {
      &.enlarge > img {
        transform: scale(1.4) translateX(-10px) rotate(2deg);
      }

      > img {
        transform: scale(1.15) rotate(2deg);
      }
    }

    &.small {
      flex: 1;
    }

    &.large {
      flex: 4;
    }

    &.enlarge > img {
      transform: scale(1.35) translateX(-10px);
    }

    > img {
      transition: 0.5s;
      width: 100%;
      transform: scale(1.15);
    }
  }
}
