@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;900&display=swap');

@import "variables";
@import "sections";
@import "cards";
@import "pocket";
@import "queries";

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 20px;
}

.app {
  min-height: 100vh;

  background-image: $bkg-image1;
  background-size: cover;
  background-position: 20% 0;
  background-repeat: no-repeat;

  &.dark-theme {
    background-color: $black;
    color: white;
    background-image: none;
  }
}

.white-stopper {
  width: 100%;
  display: flex;
  justify-content: center;

  > .stopper-svg {
    fill: #00000050;
    height: 50px;
    width: auto;

    .dark-theme & {
      fill: #ffffff50
    }
  }
}

.big-text {
  background-color: $white;
  line-height: 130px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: clamp(1em, 7.5vw, 5em);
  font-weight: bolder;
  text-transform: uppercase;
  text-align: center;
  cursor: default;

  .dark-theme & {
    background-color: $white;
    //background-image: $bkg-image2;
    //background-size: cover;

    > span {
      background-image: $bkg-image2;
      //background-image: none;
      //background-color: $white;
    }
  }

  > span {
    background-image: $bkg-image1;
    background-position: 0 90%;
    background-size: cover;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }

  &.top-margin {
    margin-top: 30px;
  }

  &.bottom-margin {
    margin-bottom: 30px;
  }
}

a {
  background: linear-gradient(90deg, #ffffff30 50%, transparent 50%) 100% 0;
  background-size: 200% 100%;

  width: 180px;

  outline: none;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid white;
  font-size: 1.05em;
  transition: 0.2s;
  color: black;
  text-decoration: none;

  display: flex;
  align-items: center;
  justify-content: center;

  .dark-theme & {
    color: white;
  }

  &:hover {
    background-position: 0 0;
  }
}

.bold {
  font-weight: 600;
}

.intermodal {
  background-color: white;
  position: fixed;
  z-index: 10;
  width: 100%;
  height: 100vh;

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  transition: transform 0.5s, background-color 0.5s;

  &.hidden {
    transform: translateY(-100%);
  }

  > .text {
    text-shadow: 0 2px 2px #00000050;
    color: #e5e5e5;
    font-weight: bold;
    font-size: clamp(12px, 10vw, 5em);
    margin-bottom: 20px;
  }

}

.intermodal.dark {
  background-color: $black;

  > .text {
    text-shadow: 0 2px 2px #ffffff50;
    color: $white;
  }
}

.theme-btn {
  cursor: pointer;

  position: absolute;
  top: clamp(5px, 2vw, 20px);
  right: clamp(5px, 2vw, 20px);

  width: clamp(30px, 5vw, 50px);
  height: clamp(30px, 5vw, 50px);
  overflow: hidden;

  border-radius: 50%;
  background-color: white;

  display: flex;
  align-items: center;
  justify-content: center;


  &:hover {
    > svg {
      fill: #00000070;
    }
  }

  > svg {
    transition: 0.2s;
    width: 40px;
    height: 40px;
    fill: #00000050;
  }
}

footer, header {
  @extend .big-text;

  > .filled-stopper {
    background-size: auto 100%;
    margin: 0 2%;
    height: 1em;
    width: 0.4em;
    transition: 0s;

    .dark-theme & {
      background-image: $bkg-image2;
    }
  }
}

.filled-stopper {
  background-image: $bkg-image1;
  background-repeat: no-repeat;
  background-position: 0 -60%;
  transition: 1s;

  mask: url('../assets/stopper_clip.svg');
  mask-size: contain;
  mask-repeat: no-repeat;

  width: 100px;
  height: 265px;

  &.loaded {
    background-position: 0 0;
  }
}
